.login--form{
    padding: 2rem;
}



.rs-input-groups{
    background-color: #f0f0f0;
    margin: 1.5rem 0;
    padding:.5rem 1rem .25rem;
    display: flex;
    align-items: flex-start;
    border-radius: 8px;

}

.rs-input-groups > i{
    font-size: 1.25rem;
    width: 40px;
    margin-top: .5rem;
    color: #0003;
}

.rs-input-groups > div > label{
    opacity: .5;
}

.rs-input-groups > div > input{
    padding-top: 0 !important;
}

.rs-input-groups > div > input:focus{
    border: none;
    box-shadow: none;
    outline: none;
}



.ls-2{
    font-weight: 300;
    font-size: 1rem;
}





/* searc reservation */
.rs-back-btn{
    font-size: 1.2rem;
}

.rs-back-btn i{
    font-size: .9rem;
    position: relative;
    top: -1px;
    margin-right: .5rem;
}

.rs-search-inputs {
    border-radius: 25px;
    padding: .5rem 1rem;
}
.rs-search-inputs input{
    font-size: 14px;
}
.rs-search-inputs input:focus{
    outline: none;
    box-shadow: none !important;
}


#rs-reservation-list {
    font-size: 12px;
}

#rs-reservation-list td span{
    border: 1px solid #ddd;
    box-shadow: 9 10px 20px -5px #0003;
    border-radius: 20px;
    margin:0 6px 6px 0;
    padding: 3px 9px;
    display: inline-block;
}

#rs-reservation-list tbody tr{
    box-shadow: 0 0 15px -5px #0002;
    border-bottom: 1px solid #0009;
}

#rs-reservation-list tbody td{
    padding: 20px 10px;
}

select:focus{
    box-shadow: none !important;
}