button:focus{
    box-shadow: none !important;
}

.dropdown-menu{
    border-radius: 10px;
}

.dropdown-item{
    font-weight: 500;
}


.rs-page-header{
    height: 250px;
    border-radius: 20px;
    position: relative;
    background-color: rgb(210, 221, 221);
    margin-bottom: 3rem;
}

.rs-page-header h2{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin-bottom: 0;
    color: #000;
    font-size: 3rem;
    letter-spacing: 2px;
}

.rs-item-img{
    width: 100%;
    height: 150px;
    border-radius: 20px;
    object-fit: cover;
    object-position: center;
    margin-bottom: 1rem;
}


.rs-item-img + div{
    padding: .2rem 1rem 5rem;
    text-align: center;
}

.rs-item-desc{
    opacity: .5;
    margin-bottom: 1rem;
}

.rs-item-btn{
    font-weight: 500;
    letter-spacing: 1px;
    border-radius: 10px !important;
    margin: 0 0 1rem;
}