.bg-gray{
    background-color: #f0f0f0;
}

.feedback-container label{
    font-weight: bold;
    letter-spacing: 2px;
    color: dodgerblue;
}

.feedback-container h5{
    font-weight: 300;
}

.feedback-container{
    background-color: #fff;
    padding: 2rem 3rem;
    border-radius:40px;
    position: relative;
    margin-bottom: 2rem;
    box-shadow: 0 10px 20px -5px #0002;
}


.feedback-container i:first-child{
    position: absolute;
    top: 10px;
    left: 10px;
    opacity: .1;
    font-size: 3rem;
}


.feedback-container i:last-child{
    position: absolute;
    bottom: 10px;
    right: 10px;
    opacity: .1;
    font-size: 3rem;
}